#app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0px;
  background: url(/static/img/Background2:2.d2249ab.svg);
  background-size: cover;
  overflow:hidden;
}
body{
  background:linear-gradient(10deg, rgb(139, 185, 255),rgb(255, 107, 188));
  background:#340099;
  background-size: cover;
  margin: 0px;
  padding: 0px;
}
h1{
  color:white;
  font-weight: 800;
}
h2{
  color:rgba(255, 255, 255, 0.719);
}
a{
  cursor: pointer;
  text-decoration: none;
  color:inherit;
}
button{
  cursor: pointer;
}
li{
  list-style: none;
}
@media screen and (max-device-width: 1024px){
body{
    background: linear-gradient(10deg,rgb(139, 185, 255), rgb(225, 107, 255));
}
#app{
    background-position: -200px 200px;
}
}

.copywrite{
    color:white;
}
.repo{
    opacity: 0.5;
}
.repo img{
    max-width: 20px;
}
.schedule{
    border:solid 5px white;
    padding:1rem;
    border-radius: 3rem;
    color:white;
    font-size: 1.5rem;
    font-weight: 800;
    -webkit-box-shadow: 0px 70px 60px #00000080;
            box-shadow: 0px 70px 60px #00000080;
}
.alt-bg{
    left: 0;
    top:0px;
    width: 100vw;
    position: fixed;
    z-index: 0;
}
.alt-bg1{
    top:0;
    position:-webkit-sticky;
    position:sticky;
    z-index: 1;
}
.schedule-div{
    margin: 2rem 0;
}
.spacer{
    height: 5rem;
}
.header-button{
    background: rgb(0, 255, 183);
    border-radius: 100px;
    color:white;
    border:none;
    font-weight: 600;
    padding: 0.3rem 1rem 0.5rem 1rem;
    font-size: 1.5rem;
}
.header-button-empty{
    background: none;
    border-radius: 100px;
    -webkit-box-shadow: 0 0 0 4px white inset;
            box-shadow: 0 0 0 4px white inset;
    color:white;
    border:none;
    font-weight: 600;
    padding: 0.3rem 1rem 0.5rem 1rem;
    font-size: 1.5rem;
}
.quote{
    padding: 2rem;
    color:white;
    /* font-family: Avenir, Helvetica-Neue; */
    font-weight: 400;
    font-style: normal;
    font-family: medium-content-serif-font,Georgia,Cambria,"Times New Roman",Times,serif;
    font-size: 35px;
    width: 60vw;
    margin: auto;
}
.logo{
    width: 10rem;
}
.squiggle{
    width: 300px;
    padding:3rem;
}
.profile-logo{
    width: 16rem;
}
.title{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
}
.header{
    position: relative;
    z-index: 2;
    padding:2rem 0rem;
    margin:0px;
    text-align: center;
}
.headline{
    color:white;
    font-size: 3rem;
    padding: 2rem;
    font-weight: 100;
}
.contact{
    position: relative;
    z-index: 2;
    width: 100%;
    /* display: flex; */
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    background:rgb(24, 24, 24);
    margin: 0px;
    /* padding:0px; */
    padding: 1rem 0;
    /* height: 50vh; */
}
.contact li{
    padding:1rem;
}
.headline span{
    padding:1rem;
}
.name{
    color: white;
    font-weight: 900;
    font-size: 4rem;
    text-align: left;
    padding-left: 2rem;
}
.socials{
    position: relative;
    z-index: 2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 70vw;
    margin: auto;
    padding: 3rem;
}
.socials * {
    max-width: 70px;
}
@media screen and (max-device-width: 1024px){
.schedule{
    position: relative;
    font-size: 1rem;
    padding:0.5rem;
    border:solid 3px white;
    bottom: 0;
}
.headline{
    font-size: 2.5rem;  
    padding: 3rem 0.5rem;
}
.name{
    font-size:3.5rem;
    text-align: center;
    padding:0px;
}
.title{
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.header{
    /* height:50vh; */
}
.text{
    width: 100vw;
}
.quote{
    padding:1rem;
    width: auto;
}
.socials{
    padding:1rem;
    width:90vw;
}
.socials *{
    width: 30px;
}
.alt-bg{
    width: 1000px;
}
}


