}


.badges{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        -ms-flex-line-pack: center;
            align-content: center;
        padding:1rem 2rem;
}
.section{
        position: relative;
        z-index: 2;
        /* background: rgba(255, 255, 255, 0.9); */
}
.projects{
        min-height: 500px;
        width:100vw;
        padding: 1rem 0;
        margin: 1rem 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: start;
            -ms-flex-align: start;
                align-items: flex-start;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
.project{
        position: relative;
        width:25vw;
        /* height: 25vw; */
        /* overflow: hidden; */
        background: white;   
        margin:1rem;
        border-radius: 10px;
        overflow: hidden;
        -webkit-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.171);
                box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.171);
}
.project img{
        width:100%;
        -webkit-filter:opacity(0.8);
                filter:opacity(0.8);
        -webkit-transition: -webkit-filter 0.3s ease-in-out;
        transition: -webkit-filter 0.3s ease-in-out;
        transition: filter 0.3s ease-in-out;
        transition: filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
}
.project img:hover{
        -webkit-filter:opacity(1);
                filter:opacity(1);
}
.badges *{
        max-width: 20px;
        max-height: 20px;
        -webkit-filter:opacity(0.4) !important;
                filter:opacity(0.4) !important;
}
